.main-chat-div {
    height: 100%;
    width: 450px;
    position: relative;
}

.kdcui-chat-frame {
    width: 100%;
    flex-grow: 1;
    flex-direction: column;
    display: flex;
}

.main-chat-sidebar {
    position: fixed;
    height: calc(100% - 48px);
    z-index: 1021;
    top: 48px;
    right: 0;

}

.main-chat-full {
    position: fixed;
    right: 0;
    top: 48px;
    width: 100%;
    height: calc(100% - 48px);
    z-index: 1021;
}


.main-chat-new_designer-sidebar {
    position: fixed;
    height: calc(100% - 42px);
    z-index: 1021;
    top: 42px;
    right: 0;

}

.main-chat-new_designer-full {
    position: fixed;
    right: 0;
    top: 42px;
    width: 100%;
    height: calc(100% - 42px);
    z-index: 1021;
}

.main-chat-home {
    width: 100%;
    height: 100%;
}


.kdcui-chat-pro-frame-main {
    height: 100%;
    width: 450px;
    position: relative;
}

.privacy_agreement_header {
    height: 50px;
    display: flex;
    font-size: 16px;
    color: #212121;
    line-height: 21px;
    font-weight: 600;
}

.privacy_agreement_body {
    height: 30px;
    display: flex;
    font-size: 12px;
    color: #3b3b3b;
    line-height: 21px;
    font-weight: 400;
}

.privacy_main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.privacy_btn {
    margin-top: 18px;
}

.main_question_text {
    font-size: 28px;
    color: #212121;
    font-weight: 600;
    margin-left: 8px;
}

.main_question_cosmic {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main_question_card_d {
    display: flex;
    flex-direction: column;
}

.main_question_des {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 8px;
    margin-top: 8px;
}

.text_need_change_style {
    font-size: 14px;
    color: #212121;
    line-height: 21px;
    font-weight: 400;
    letter-spacing: 8px;
}

.text_need_change_style_en {
    font-size: 14px;
    color: #212121;
    line-height: 21px;
    font-weight: 400;
    margin-right: 4px;
}

#text_not_change_style_A {
    letter-spacing: 2px;
    color: #212121;
}

#text_not_change_style_A_en {
    color: #212121;
}

#text_not_change_style_I {
    margin-right: 8px;
    color: #212121;
}

#text_not_change_style_I_en {
    margin-right: 4px;
    color: #212121;
}

.main_question_card_full {
    margin-top: 16px;
    width: 770px;
    color: #e5e5e5;
}

.main_question_card {
    margin-top: 16px;
    width: 400px;
    max-width: 770px;
    color: #e5e5e5;
}

.main_question_card_text {
    background: #FFFFFF;
    border: 1px solid rgba(231, 234, 239, 1);
    border-radius: 4px;
    width: 100%;
    height: 28px;
    margin-bottom: 6px;
    margin-top: 6px;
    font-family: MicrosoftYaHei;
    font-size: 12px;
    color: #404151;
    letter-spacing: 0;
    line-height: 18px;
    font-weight: 400;
    display: flex;
    align-items: center;
    padding-left: 8px;

}


.main_question_card_text:hover {
    cursor: pointer;
    /* 鼠标悬停时变为手掌模样 */
    color: #5582f3;
    /* 鼠标悬停时字体变为蓝色 */
}

.main_question_card_head {
    display: flex;
    align-items: center;
    height: 44px;
    background-color: #ECF7FF;
}

.main_question_card_head_img {
    width: 20px;
    height: 20px;
    margin-right: 8px;
}

.main_question_card_head_title {
    font-size: 16px;
    color: #212121;
    font-weight: 600;

}

.main_question_card_ref_icon {
    height: 12px;
    width: 12px;
}

.main_question_card_ref_text {
    font-family: MicrosoftYaHei;
    font-size: 12px;
    color: #666666;
    letter-spacing: 0;
    line-height: 18px;
    font-weight: 400;
    margin-left: 4px;
}

.main_question_card_des_text {
    font-family: MicrosoftYaHei;
    font-size: 14px;
    color: #212121;
    letter-spacing: 0;
    line-height: 20px;
    font-weight: 400;
    height: 36px;
    width: 100%;
    background-color: rgba(224, 242, 255, .4);
    display: flex;
    align-items: center;
    border-radius: 4px;
    opacity: 0.6;
    margin-bottom: 8px;
    padding-left: 12px;
    position: relative;

}

.main_question_card_des_text::before {
    background: #3c6ded;
    border-radius: 50%;
    content: "";
    height: 6px;
    top: 3px;
    transform: translateY(-50%);
    width: 6px;
    position: relative;
    margin-right: 4px;
}

.main_question_card_ref {
    height: 24px;
    width: max-content;
}

.main_question_card_ref:hover {
    cursor: pointer;
    /* 鼠标悬停时变为手掌模样 */

}

.chat-ai-cosmic {
    cursor: pointer;
}

.dislike-card-main {
    min-width: 386px;
    margin-top: 8px;
    margin-left: 32px;
    border-radius: 8px;
    max-width: 690px;
}

.dislike-card-title {
    font-size: 14px;
    color: #3b3b3b;
    display: flex;
    align-items: center;
}

.dislike-card-questions {
    display: flex;
    align-items: center;
    margin-top: 8px;
    margin-bottom: 8px;
}

.dislike-card-questions-item {
    border: 1px solid #888d92;
    height: 28px;
    align-items: center;
    text-align: center;
    justify-content: center;
    display: flex;
    margin-right: 4px;
    border-radius: 4px;
    padding-left: 4px;
    padding-right: 4px;
    cursor: pointer;
    color: #212121;
}

.dislike-card-questions-item-active {
    border: 1px solid #888d92;
    height: 28px;
    align-items: center;
    text-align: center;
    justify-content: center;
    display: flex;
    margin-right: 4px;
    border-radius: 4px;
    padding-left: 4px;
    padding-right: 4px;
    cursor: pointer;
    color: #276ff5;
    border-color: #276ff5;
}

.dislike-card-questions-item-submit {
    color: #5797ff;
    cursor: pointer;
    margin-top: 12px;
    font-size: 14px;
}

.bos-designer-cosmic {
    align-items: center;
    color: #fff;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    font-size: 12px;
    height: 50px;
    justify-content: center;
    position: relative;
    width: 64px;
}

.bos-designer-cosmic:hover {
    background-color: hsla(0, 0%, 100%, .35);
}


.right-icon-container-disabled {
    opacity: 0.3;
    /* 降低透明度 */
    cursor: not-allowed;
    /* 更改鼠标指针为禁止符号 */
    pointer-events: none;
    /* 禁止鼠标事件 */
}


.kdcui-chat-card-iframe {
    border: 0;
}

.tools_disabled {
    pointer-events: none;
    cursor: default;
}

#main-chat-sidebar-div{
    flex-grow: 1;
    flex-direction: column;
    display: flex;
}

.reference-link{
    width: 100%;
}