.container {
  display: grid;
  grid-template-columns: repeat(
    auto-fill,
    minmax(60px, 1fr)
  ); /* 这会创建尽可能多的列，列宽至少为200px且尽量均等分布 */
  grid-gap: 10px; /* 格子之间的间隔 */
  grid-auto-flow: row; /* 在需要时自动将网格项目移动到新的行中 */
}
.svgItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.title {
  text-align: center;
}
